<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="1000"
    >
      <div slot="title">系统内商品</div>
      <div>
        <textarea v-model="ori_data" cols="100" rows="3" style="margin-right: 15px;"></textarea>
        <a-button size="large"  @click="parseData">解析对象</a-button>
      </div>
      <div class="modal-body">
        <h3>基本信息</h3>

        <div class="row" style="display: flex; flex-direction: row; flex-wrap: wrap;">
          <div class="colums" style="display: flex;" v-for="item,index in form.products" :key="item.productId+index">
            <img :src="item.imageUrl" style="width: 90px; height: 90px;" alt="" />
            <div style="display: flex; flex-direction: column;">
              <div style="display: flex;">
                <span class="label">变体属性:</span>
                <span class="value">{{ item.attr }}</span>
              </div>
              
              <div style="display: flex;">
                <span class="label">产品ID:</span>
                <span class="value">{{ item.productId }}</span>
              </div>

              <div style="display: flex;">
                <span class="label">specId:</span>
                <span class="value">{{ item.specId }}</span>
              </div>

              <div style="display: flex;">
                <span class="label">1688SKU:</span>
                <span class="value">{{ item.skuId }}</span>
              </div>
            </div>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 80px;">sku前缀:</span>
            <a-input v-model="form.sku_front_name" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 80px;">参考重量:</span>
            <a-input v-model="form.reference_weight" />
          </div>

          <div class="columns">
            <span class="label" style="width: 80px;">参考价格:</span>
            <a-input v-model="form.reference_cost" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">采购链接:</span>
            <div>
              <div v-for="link,index in purchase_links" :key="link+index">
                <span>链接{{ index+1 }}号. </span>
                <span>链接:</span>
                <a-input v-model="purchase_links[index].link" style="width: 200px; margin-right: 10px;"/> 
                <span>备注:</span>
                <a-input v-model="purchase_links[index].msg" style="width: 150px; margin-right: 10px;"/> 
                <a-button type="danger" icon="delete" size="small" @click="delpurchaseLink(index)">删除</a-button>
              </div>
            </div>
          </div>
          <a-button icon="edit" size="small" @click="addPurchaseLink">新增采购链接</a-button>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">采购平台:</span>
            <span class="value">1688</span> 
          </div>

          <div class="columns">
            <span class="label" style="width: 115px;">供应商名称:</span>
            <span class="value">{{ form.supplier_name }}</span>
          </div>
        </div>

    </div>
    </a-modal>
  </div>
</template>

<script>
import {addDataWith1688Data } from '@/api/operation'
import { message } from "ant-design-vue";

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      attrList:[],
      purchase_links:[],
      ori_data:""
    };
  },
  computed: {
  },
  watch:{
    purchase_links:{
      handler(newValue){
        this.form.purchase_link = JSON.stringify(newValue)
      },
      deep:true
    },
    visible(newValue) {
      this.purchase_links = []
      thus.ori_data = ""
      this.form = {}
    }
  },
  methods: {
    confirm() {
      console.log(this.form)
      addDataWith1688Data({...this.form,"purchase_platform":"1688"}).then(
        res => {
          message.success(res.msg)
          this.$emit('update', false);
        }
      )
    },
    cancel() {
      this.$emit('cancel', false);
    },

    addAttrList(){
      this.attrList.push([])
    },

    delAttrList(){
      this.attrList.pop()
    },

    addAttr(attr){
      attr.push("")
    },
    
    delAttr(attr){
      attr.pop()
    },

    addPurchaseLink(){
      this.purchase_links.push({link:"",msg:""})
    },
    delpurchaseLink(index){
      this.purchase_links.splice(index,1)
    },
    parseData(){
      let data = JSON.parse(this.ori_data)
      console.log(data)
      this.form.supplier_name = data.supplier_name;
      this.purchase_links.push({link:data.url, msg:""})
      this.form.products = data.products
    }

  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.label{
  margin-right: 5px
}

.value{
  color: green;
  margin-right: 20px;
}
</style>